.box {
    position: relative;
    width: 300px;
    height: 400px;
    background-color:azure;
    border-radius: 10px;
    margin: 50px auto;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .inputData {
    margin-bottom: 20px;
  }
  
  .inputFeild {
    width: 80%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    outline: none;
  }
  
  .info {
    position: relative;
    color: #333;
  }
  
  .location {
    font-size: 1.5rem;
  }
  
  .temp {
    font-size: 3rem;
    margin: 20px 0;
  }
  
  .tempmin_max {
    font-size: 1rem;
    margin-bottom: 20px;
  }
  
  .fa-solid fa-street-view {
    margin-right: 5px;
  }
  
  .wave-one,
  .wave-two,
  .wave-three {
    position: absolute;
    width: 100%;
    height: 100px;
    bottom: 0;
  }
  
  .wave-one {
    background: url('https://www.transparenttextures.com/patterns/asfalt-dark.png');
    background-size: 100% 100px;
    animation: drift 3000ms infinite linear;
    z-index: 1;
    opacity: 0.3;
  }
  
  .wave-two {
    background: url('https://www.transparenttextures.com/patterns/asfalt-dark.png');
    background-size: 100% 100px;
    animation: drift 3000ms infinite linear;
    z-index: 2;
    opacity: 0.3;
  }
  
  .wave-three {
    background: url('https://www.transparenttextures.com/patterns/asfalt-dark.png');
    background-size: 100% 100px;
    animation: drift 3000ms infinite linear;
    z-index: 3;
    opacity: 0.3;
  }
  
  @keyframes animate {
    0% {
      background-position-x: 0;
    }
    100% {
      background-position-x: 1000px;
    }
  }
  